.stars {
  margin: 0;
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(/images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 4px;
  }
  
  &--4 &__star:nth-child(-n+4) {
    background-image: url(/images/star-active.svg);
  }
}
